@use '../base/variables' as v;
@use '../base/mixins' as m;

.inicio{
    background-image: url(../../build/img/header.jpg);
    
}
.webp .inicio{
    background-image: url(../../build/img/header.webp);
    
}

.header{
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;
        @include m.tablet{
            min-height: 70rem;
        }
    .contenido-header{
        @include m.tablet{
            min-height: 70rem;
        }
    }
    }
}

.contenido-header{
    color: v.$blanco;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

    

    h1{
        text-align: left;
        font-weight: v.$bold;
        line-height: 2;
        max-width:50rem ;
    }
}