@use '../base/variables' as v;
@use '../base/mixins' as m;


.alerta{
        color: v.$blanco;
        font-size: 2rem;
        text-align: center;
        font-weight: v.$bold;
        text-transform: uppercase;
        margin-bottom: 1rem;
    &.error{
        background-color: v.$rojo;
        
    }
    &.exito{
        background-color: v.$verde;
    }
}

table.propiedades{  
    margin-top: 4rem;
    width: 100%;
    border-spacing:0 ;

    thead{
        background-color: v.$verde;
    }
    th{
        color: v.$blanco;
    }
    td{
        background-color: lighten(v.$verde, 60%);
        border-right: 1px solid v.$verde;
        border-bottom: 1px solid v.$verde;
        text-align: center;
    }
    .imagen-tabla{
        width: 10rem;
        display: block;
        margin: 0 auto;
    }
}

.btn-acciones{
    width: 15rem;
    display: block;
    margin: 1rem auto;
    border-radius: 1rem;
}

