@use '../base/variables' as v;
@use '../base/mixins' as m;

.testimonial{
    background-color: v.$verde;
    font-size: 2.4rem;
    padding: 2rem;
    color: v.$blanco;
    border-radius: 2rem;
    blockquote{
        padding-left:5rem ;
        position: relative;
        &:after{
            content: '';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            top: 0;
            left: -2rem;
            
        }
    }
    p{
        text-align: right;
        color: v.$blanco;
    }
    
}
