@use '../base/variables' as v;
@use '../base/mixins' as m;


@include m.tablet{
    
    .contenedor-anuncios{
    h2{
        text-align: center
    }    
    @include m.grid(3,2rem);
    grid-template-columns: minmax(0, auto) minmax(0, auto) minmax(0, auto);
        grid-template-rows: minmax(0, auto);
    }
}

.anuncio{
    background-color: lighten(v.$gris,5) ;
    border: 1px solid darken(v.$gris,10);
    .contenido-anuncio{
        padding: 2rem;
    }
    
    h3,p{
        margin:0 ;
        text-align: center;
        word-wrap: break-word;
    }
    
}
.precio{
    color: v.$verde;
    font-weight: v.$bold;
    font-size: 3rem;
}
.iconos-caracteristicas{
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 90rem;
    li{
        flex: 0.5;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            flex: 0 0 3rem;
            margin-right: 1rem;
            object-fit: cover;
        }
        p{
            font-weight: v.$black;
        }
    }
}
.dark-mode .contenido-ad{
    .precio{
        text-align: center;
    }

    .icono-anun{
        display: block;
        margin: 3rem  5rem;
    }
    p{
        width: 90%;
        margin: 0 auto;
    }
}